import React, { useRef, useState, useEffect } from "react";
import { Col, Row, Container, Button, Form } from "react-bootstrap";
import { navigate } from "gatsby";

import "../components/pages-css/sCita.css";
import val from "../components/validadores";
import Layout from "../components/layout.js";
import Seo from "../components/seo";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Keyboards from "../components/Keyboards.js";
import Timer from "../components/timer.js";
import api from "../components/actions/riveroapi.js";

export default function RegistrarVehiculo(){

    const [show, setShow] = useState(false);
    const [modal, setModal] = useState(false);
    const [errorInput, setErrorInput] = useState(false);
    const [error, setError] = useState(0);
    const [click, setClick] = useState(false);
    const [modelitos, getModelitos] = useState();
    const [modeo, setModeo] = useState();

    const [formInputError, setFormInputError] = useState({
        Modelo: false,
        Color: false,
        Anio: false,
        VINDatos: false,
        Placas: false,
        Kilometraje: false
    })

    const [optionsKeyboard, setOptionsKeyboard] = useState({
        inpName: "",
        layoName: "default",
        name: "",
        value: {
            Modelo: undefined,
            Color: undefined,
            Anio: undefined,
            VINDatos: undefined,
            Placas: undefined,
            Kilometraje: undefined,
        }
    })

    const [datoscliente, setDatosCliente] = useState({})

    const refForm = useRef();

    useEffect(() => {
        if (typeof window !== 'undefined') {
            //console.log('we are running on the client', JSON.parse(localStorage.getItem("informacion")));
            setOptionsKeyboard({
              ...optionsKeyboard,
              value: {
                Modelo: localStorage.getItem('Modelo') || "",
                Color: localStorage.getItem('Modelo') || "",
                Anio: localStorage.getItem('Anio') || "",
                VINDatos: localStorage.getItem('VINDatos') || "",
                Placas: localStorage.getItem('Placas') || "",
                Kilometraje: localStorage.getItem('Kilometraje') || "",
              }
            });

            let informacion = JSON.parse(localStorage.getItem("informacion"))

            setDatosCliente({
                ...datoscliente,
                data: {},
                id: 0,
                codigo: 0,
                datosCliente: {
                    Nombre: informacion.datosCliente.Nombre,
                    RFC: informacion.datosCliente.RFC,
                    DiaNacimiento: informacion.datosCliente.DiaNacimiento,
                },
                direccionCliente: {
                    Calle: informacion.direccionCliente.Calle,
                    Numero: informacion.direccionCliente.Numero,
                    Colonia: informacion.direccionCliente.Colonia,
                    CodigoPostal: informacion.direccionCliente.CodigoPostal,
                    Estado: informacion.direccionCliente.Estado,
                    Municipio: informacion.direccionCliente.Municipio,
                    Pais: "MEXICO"
                },
                clienteContacto: {
                    Celular: informacion.clienteContacto.Celular,
                    Telefono: informacion.clienteContacto.Telefono,
                    Correo: informacion.clienteContacto.Correo,
                    Preferencia: "WhatsApp"
                },
            })
        } else {
            //console.log('we are running on the server');
        }
        api.getModelos().then((resp) => getModelitos(resp.data))
    }, [])

    const doubled = modelitos?.map((obj, i) => {
        ////console.log("Tengo esto, ", obj.modelo)
        return(
            <option key={i} value={obj.modelo}>{obj.modelo}</option>
        )
    });

    function continuar(){

        let params = {
            Color: optionsKeyboard.value.Color,
            Anio: optionsKeyboard.value.Anio,
            VINDatos: optionsKeyboard.value.VINDatos,
            Placas: optionsKeyboard.value.Placas,
            Kilometraje: optionsKeyboard.value.Kilometraje
        }

        val.ValidadorCamposDatosAutos(params).then((resp) => {

            setFormInputError({
                Color: resp.color.boolean,
                Anio: resp.anio.boolean,
                VINDatos: resp.vin.boolean,
                Placas: resp.placas.boolean,
                Kilometraje: resp.kilometraje.boolean
            })

            if(resp.error === 0){
                setError(resp.error);
                setModal(true);
            } else if (resp.error === 5) {

                const parametrosNuevos = {
                    data: {
                        Marca: "Chevrolet",
                        Modelo: modeo,
                        Color: optionsKeyboard.value.Color,
                        Anio: optionsKeyboard.value.Anio,
                        VINDatos: optionsKeyboard.value.VINDatos,
                        Placas: optionsKeyboard.value.Placas,
                        Kilometraje: optionsKeyboard.value.Kilometraje,
                    },
                    id: 0,
                    codigo: 0,
                    datosCliente: {
                        Nombre: datoscliente.datosCliente.Nombre,
                        RFC: datoscliente.datosCliente.RFC,
                        DiaNacimiento: datoscliente.datosCliente.DiaNacimiento,
                    },
                    direccionCliente: {
                        Calle: datoscliente.direccionCliente.Calle,
                        Numero: datoscliente.direccionCliente.Numero,
                        Colonia: datoscliente.direccionCliente.Colonia,
                        CodigoPostal: datoscliente.direccionCliente.CodigoPostal,
                        Estado: datoscliente.direccionCliente.Estado,
                        Municipio: datoscliente.direccionCliente.Municipio,
                        Pais: "MEXICO"
                    },
                    clienteContacto: {
                        Celular: datoscliente.clienteContacto.Celular,
                        Telefono: datoscliente.clienteContacto.Telefono,
                        Correo: datoscliente.clienteContacto.Correo,
                        Preferencia: "WhatsApp"
                    },
                }
                
                navigate("/servicio-a-realizar")
                localStorage.setItem('informacion', JSON.stringify(parametrosNuevos));
            }

        })

    }

    return(
        <Layout backUrl={"/nuevo-usuario"}>
            <Seo title="Auto servicio" />
            <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
            {/*<Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>*/}
                <Row className="row-cita-arriba">
                    <form className="container-cita">
                        <Row>
                            <Col className="titulo-cita"><h1 className="titulo-h1">Datos del auto</h1></Col>
                        </Row>
                        <Row className="titulo-cita">
                            <Container>
                                <Row>
                                    <Col lg={3}>
                                        Marca:
                                        <input
                                        autoComplete="off"
                                        type="text"
                                        id={formInputError.Nombreform === true ? "inputContactoError" : "inputContacto"}
                                        disabled
                                        placeholder="Chevrolet"
                                        className="form-control"
                                        />
                                    </Col>
                                    <Col lg={3}>
                                    <div style={{paddingBottom:10}}>Modelo: </div>
                                        <Form.Select id={"inputContacto"} onChange={(e) => {setModeo(e.target.value)}}>
                                            <option>Seleccionar modelo</option>
                                            {doubled}
                                        </Form.Select>
                                    </Col>
                                    <Col lg={3}>
                                        Color:
                                        <input
                                        autoComplete="off"
                                        type="text"
                                        id={formInputError.Color === true ? "inputContactoError" : "inputContacto"}
                                        className="form-control"
                                        onClick={() => {setShow(true); setOptionsKeyboard({...optionsKeyboard, layoName: "default"}) }}
                                        onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "Color"})}
                                        value={optionsKeyboard.value.Color || ""}
                                        />
                                    </Col>
                                    <Col lg={3}>
                                        Año:
                                        <input
                                        autoComplete="off"
                                        type="text"
                                        id={formInputError.Anio === true ? "inputContactoError" : "inputContacto"}
                                        className="form-control"
                                        onClick={() => {setShow(true); setOptionsKeyboard({...optionsKeyboard, layoName: "phone"}) }}
                                        onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "Anio"})}
                                        value={optionsKeyboard.value.Anio || ""}
                                        />
                                    </Col>
                                </Row>
                                <Row>
                                    <Col lg={3}>
                                        VIN:
                                        <input
                                            autoComplete="off"
                                            type="text"
                                            id={formInputError.VINDatos === true ? "inputContactoError" : "inputContacto"}
                                            className="form-control"
                                            onClick={() => {setShow(true); setOptionsKeyboard({...optionsKeyboard, layoName: "vin"}) }}
                                            onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "VINDatos"})}
                                            value={optionsKeyboard.value.VINDatos || ""}
                                        />
                                    </Col>
                                    <Col lg={3}>
                                        Placas:
                                        <input
                                        autoComplete="off"
                                        type="text"
                                        id={formInputError.Placas === true ? "inputContactoError" : "inputContacto"}
                                        className="form-control"
                                        onClick={() => {setShow(true); setOptionsKeyboard({...optionsKeyboard, layoName: "vin"}) }}
                                        onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "Placas"})}
                                        value={optionsKeyboard.value.Placas || ""}
                                        />
                                    </Col>
                                    <Col lg={3}>
                                        Kilometraje:
                                        <input
                                        autoComplete="off"
                                        type="text"
                                        id={formInputError.Kilometraje === true ? "inputContactoError" : "inputContacto"}
                                        className="form-control"
                                        onClick={() => {setShow(true); setOptionsKeyboard({...optionsKeyboard, layoName: "phone"}) }}
                                        onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "Kilometraje"})}
                                        value={optionsKeyboard.value.Kilometraje || ""}
                                        />
                                    </Col>
                                    <Col lg={3}></Col>
                                </Row>
                                {show === true ?
                                    <center className="pt-5">
                                        <Keyboards options={optionsKeyboard} setOptionsKeyboard={setOptionsKeyboard} optionsKeyboard={optionsKeyboard}/>
                                    </center>
                                : null }
                            </Container>
                        </Row>
                    </form>
                </Row>
                <Button ref={refForm} onClick={() => continuar()} style={{display: 'none'}}></Button>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="tiene"/>
                </Row>
            </Container>
            <ModalEmergente modal={modal} error={error} setModal={setModal} setClick={setClick}/>
        </Layout>
    )

}